import type OpenFin from '@openfin/core';

import { computeIndicatorBounds } from './bounds';
import { IndicatorOptions } from './types';
import { showIndicatorWindow } from './window';

/**
 * A tiny orchestration wrapper that coordinates bounds calculation and window management.
 */
export async function openIndicator(
    identity: OpenFin.Identity | undefined,
    containerEl: HTMLDivElement,
    opts: IndicatorOptions
): Promise<() => void> {
    const rect = containerEl.getBoundingClientRect();

    // The runtime occasionally reports 0×0 on first paint (WRK‑4224)
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    if (!rect.width || !rect.height) return () => {};

    const bounds = await computeIndicatorBounds(identity, { width: rect.width, height: rect.height }, opts);

    const autoCloseMs = opts.isEnterprise ? 5000 : 3000;
    return showIndicatorWindow(bounds, identity, autoCloseMs);
}
