import React, { FC, useRef } from 'react';

import { useRouter } from 'next/router';

import styled from 'styled-components';

import type OpenFin from '@openfin/core';

import { PositioningType } from '@common/utils/positioning';

import { ShowAtType } from './types';
import { useIndicatorWindow } from './useIndicatorWindow';

const Container = styled.div`
    overflow: hidden;
`;

const ObservedContainer = styled.div`
    display: inline-block;
`;

interface Props {
    windowIdentity: OpenFin.Identity;
    showAt?: ShowAtType;
    parentWindowBounds?: OpenFin.WindowBounds;
    children: React.ReactNode;
    isEnterprise: boolean;
    positioning?: PositioningType;
}

const IndicatorContainer: FC<Props> = ({
    children,
    windowIdentity,
    parentWindowBounds,
    positioning,
    showAt,
    isEnterprise
}) => {
    const ref = useRef<HTMLDivElement>(null);
    const router = useRouter();

    const targetShowAt = showAt || isEnterprise ? ShowAtType.Below : ShowAtType.Top;

    useIndicatorWindow(router.isReady ? windowIdentity : undefined, ref, {
        showAt: targetShowAt,
        isEnterprise,
        fallbackParentBounds: parentWindowBounds,
        positioning
    });

    return (
        <Container>
            <ObservedContainer ref={ref}>{children}</ObservedContainer>
        </Container>
    );
};

export default IndicatorContainer;
