import React, { FC, useEffect } from 'react';

import Head from 'next/head';

import type OpenFin from '@openfin/core';

import { finUUID } from '@common/utils/env';
import { IndicatorIcon } from '@common/utils/indicators/showIndicator';
import { emitMenuResponse } from '@common/utils/menu-window-provider';
import { getUrlParams, Router, withRouter } from '@common/utils/router';
import { deserializeWindowBounds, WindowName } from '@common/utils/window';
import { currentOFIdentity, hide } from '@common/utils/window';

import IndicatorContainer from '@common/components/IndicatorContainer/IndicatorContainer';

import { EnterpriseIndicator } from '@enterprise/components/Indicator/EnterpriseIndicator';
import { type ResponseModalActionHandler } from '@enterprise/components/ModalContent/ResponseModal';

import { useIsWindows } from '@enterprise/hooks/useIsWindows';

interface Props {
    router: Router;
}

const IndicatorPage: FC<Props> = ({ router }) => {
    const params = getUrlParams(router.asPath);

    const message = params.message as string;
    const icon = params.icon as IndicatorIcon;
    const secondaryMessage = params.secondaryMessage as string;
    const browserWindowName = params.parentName as string;
    const winIdentity = browserWindowName ? { uuid: finUUID, name: browserWindowName } : undefined;
    const parentWindowBounds: OpenFin.WindowBounds | null = deserializeWindowBounds(params.parentBounds);

    const isWindows = useIsWindows();

    const closeCurrentIndicator = (identity: OpenFin.ApplicationEvents.WindowCreatedEvent) => {
        if (identity.name.includes(WindowName.BrowserIndicator) && identity !== fin.me.identity) {
            fin.me.isWindow && fin.me.close();
        }
    };

    // Close the current indicator whenever a new one is created
    useEffect(() => {
        fin.Application.getCurrentSync().addListener('window-created', closeCurrentIndicator);

        return () => {
            fin.Application.getCurrentSync().removeListener('window-created', closeCurrentIndicator);
        };
    }, []);

    const handleAction: ResponseModalActionHandler = async ({ name, data }) => {
        await emitMenuResponse({ actionName: name, data });
        hide(currentOFIdentity);
    };

    return (
        <IndicatorContainer windowIdentity={winIdentity} parentWindowBounds={parentWindowBounds} isEnterprise={true}>
            <Head>
                <title>OpenFin Home Workspace</title>
            </Head>
            <EnterpriseIndicator
                message={message}
                secondaryMessage={secondaryMessage}
                onAction={handleAction}
                isWindows={isWindows}
                icon={icon}
            />
        </IndicatorContainer>
    );
};

export default withRouter(IndicatorPage);
