import { useEffect, useRef } from 'react';

import type OpenFin from '@openfin/core';

import { openIndicator } from './orchestrator';
import { IndicatorOptions } from './types';

/**
 * Custom hook that manages the indicator window lifecycle.
 * Handles ResizeObserver setup and cleanup of window event listeners.
 */
export function useIndicatorWindow(
    identity: OpenFin.Identity | undefined,
    containerRef: React.RefObject<HTMLDivElement>,
    opts: IndicatorOptions
) {
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    const disposerRef = useRef<() => void>(() => {});

    useEffect(() => {
        if (!containerRef.current) return;

        const resizeObs = new ResizeObserver(async () => {
            disposerRef.current(); // dispose previous listeners/timeout
            disposerRef.current = await openIndicator(identity, containerRef.current!, opts);
        });

        resizeObs.observe(containerRef.current);

        // Initial run
        openIndicator(identity, containerRef.current, opts).then((d) => (disposerRef.current = d));

        return () => {
            resizeObs.disconnect();
            disposerRef.current();
        };
    }, [identity, opts.showAt, opts.isEnterprise, opts.positioning, opts.fallbackParentBounds, containerRef, opts]);
}
