import { css } from 'styled-components';

export const MAC_WINDOW_BORDER_RADIUS = '10px';

type BorderProps = {
    size?: number;
    dimensions?: { top?: number | false; bottom?: number | false; right?: number | false; left?: number | false };
    borderColor: string;
    backgroundColor?: string;
};

const getDimension = (defaultSize = 0, size?: number | false) => {
    if (size === false) {
        return 0;
    }
    if (size >= 0) {
        return size;
    }
    return defaultSize;
};

export const PaddingBorderMixin = ({ size, dimensions = {}, borderColor, backgroundColor }: BorderProps) => css`
    background-color: ${borderColor};
    padding-left: ${getDimension(size, dimensions.left)}px;
    padding-right: ${getDimension(size, dimensions.right)}px;
    padding-top: ${getDimension(size, dimensions.top)}px;
    padding-bottom: ${getDimension(size, dimensions.bottom)}px;
    > * {
        ${backgroundColor
            ? css`
                  background-color: ${backgroundColor};
              `
            : ''}
    }
`;
