import { useEffect, useState } from 'react';

import { isOSWindows } from '@common/utils/os';

/**
 * Custom hook that determines if the operating system is Windows.
 *
 * NOTE: This hook should only be used in non-browser window components.
 * Otherwise, opt for the selectIsWindows selector to access the Redux store.
 *
 * @returns {boolean} - Returns `true` if the operating system is Windows, otherwise `false`.
 */
export const useIsWindows = () => {
    const [isWindows, setIsWindows] = useState(true);

    useEffect(() => {
        (async () => {
            setIsWindows(await isOSWindows());
        })();
    }, []);

    return isWindows;
};
