import type OpenFin from '@openfin/core';

import { getCurrentOFWindow, getOFWindow } from '@common/utils/window';

import { IndicatorBounds } from './types';

/**
 * Moves, shows and auto‑closes the current OpenFin window.
 * Returns a disposer that callers must invoke to release resources.
 */
export async function showIndicatorWindow(
    bounds: IndicatorBounds,
    identity: OpenFin.Identity | undefined,
    autoCloseMs: number
): Promise<() => void> {
    const currWin = getCurrentOFWindow();
    await currWin.setBounds(bounds);
    await currWin.show();

    const timeout = setTimeout(() => currWin.close(), autoCloseMs);

    const disposers: Array<() => void> = [() => clearTimeout(timeout)];

    if (identity) {
        const parentWin = getOFWindow(identity);
        const close = () => currWin.close();

        ['bounds-changing', 'blurred', 'maximized', 'minimized', 'restored'].forEach((evt) => {
            parentWin.on(evt as any, close);
            disposers.push(() => parentWin.removeListener(evt as any, close));
        });
    }

    return () => disposers.forEach((fn) => fn());
}
